@import url("https://fonts.googleapis.com/css2?family=Lilita+One&family=Poppins:wght@900&display=swap");
* {
  /* border: solid 1px green; */
  box-sizing: border-box;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 1.2rem;
}

html {
  font-size: 16px;
}
@media (width <= 400px) {
  html {
    font-size: 12px;
  }
}

html, body, #root, .app {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(#eee, #ccc);
  font-family: "Lilita One", sans-serif;
  overflow: auto;
  height: unset;
  min-height: 100%;
}

main {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: center;
  width: min(18rem, 80%);
  height: 100%;
}
main #left {
  width: 0;
  height: 0;
}
main #center {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (width <= 720px) {
  main #center {
    margin-top: 1.2rem;
  }
}
@media (width <= 400px) {
  main #center {
    margin-top: 1.6rem;
  }
}
@media (width > 720px) {
  main {
    width: 100%;
    flex-direction: row;
    gap: 1.5rem;
    align-items: flex-start;
  }
  main #center {
    width: min(18rem, 40%);
  }
  main #left {
    flex: 1 1 20%;
  }
}
@media (width <= 400px) {
  main {
    width: 80%;
  }
}

#footer {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
#footer #source-link {
  text-transform: uppercase;
  text-decoration: none;
  font-size: 0.85rem;
  color: #999;
  letter-spacing: 0.04rem;
  transition: all 0.3s;
  margin-bottom: 2rem;
  margin-top: 1rem;
}
#footer #source-link:visited {
  color: #999;
}
#footer #source-link:hover {
  color: #555;
}

@media (width > 400px) {
  #footer #source-link {
    font-size: 0.6375rem;
    margin-bottom: 1.5rem;
    margin-top: 1rem;
    letter-spacing: 0.03rem;
  }
}

